<template>
    <div>
        <ContextTable @itemSelected="itemSelected"/>
        <ContextFields :item="selectedItem"/>
    </div>		
</template>

<script>
import ContextTable from '@/components/ContextTable'
import ContextFields from '@/components/ContextFields'

export default {
	name: 'ContextEditor',
	components: {
        ContextTable,
		ContextFields
	},
	mixins: [ ],
	props: { },
	data() { 
        return { 
            selectedItem: null
        } 
    },
	computed: {
        itemParents() { 
			return this.$store.getters.groups.map(g => {
				return { value: g.data.id, text: g.data.id }
			})
		},
		itemTypes() { 
			return this.$store.getters.contextTypes
				.map(t => t)
				.sort((a,b) => a.localeCompare(b)) 
		}
    },
	methods: {
        itemSelected(item) {
            this.selectedItem = item
        }        
    },
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

