<template>
    <b-container fluid>
		<b-row>			
			<b-col></b-col>
			<b-col>
				<b-form-group
					label="Filter"
					label-for="filter-input"
					label-cols-sm="3"
					label-align-sm="right"
					label-size="sm"
					class="mb-2">
					<template v-slot:label>
						<b-icon-search />
					</template>
					<b-form-input 
						size="sm"
						id="filter-input"
						v-model="filter"
						type="search"
						:placeholder="`search ${itemClass}s`"/>					
			</b-form-group>
			</b-col>
		</b-row>
		<b-row>
			<b-col>
				<b-table show-empty style="height: 200px;"
					id="datatable"
					sort-icon-left 
					striped small hover outlined selectable
					sticky-header="300px" 
					select-mode="single"
					primary-key="id"
					:items="items" 
					:fields="fields"
					:filter="filter" 			
					class="overflow-auto"
					@row-selected="rowSelected">
					<template #cell(actions)="row">
						<div class="text-right">										
							<b-icon-x-circle
								class="action mr-2" 
								:title="`delete ${itemClass}`" 
								:alt="`delete ${itemClass}`"							
								@click.stop="deleteItem(row.item)"/>					
						</div>				
					</template>
				</b-table>
				<div class="text-right">
					<b-icon-plus-circle
						class="action mr-2" 
						:title="`add ${itemClass}`" 
						:alt="`add ${itemClass}`"			
						@click.stop="createItem()"/>			
				</div>
			</b-col>
		</b-row>
	</b-container>
</template>

<script>
export default {
	name: 'ItemTable',
	components: {},
	props: {
		items: {
			type: Array,
			required: true
		},
		fields: {
			type: Array,
			required: true
		},
		itemClass: {
			type: String,
			required: false,
			default: "item",
			validator: value => ["item", "phase", "group", "subgroup", "context", "find", "sample"].includes(value)      
		}
	},
	data() { 
		return {			
			filter: "",
			selectedID: ""		
		}
	},
	computed: {			
       totalRows() { return this.items.length }
	},
	mounted() {},
	methods: {		
		createItem() {
			let self = this
			switch(self.itemClass) {
				case "phase": self.$store.dispatch('createPhase'); break;
				case "group": self.$store.dispatch('createGroup'); break;
				case "subgroup": self.$store.dispatch('createSubGroup'); break;
				case "context": self.$store.dispatch('createContext'); break;
				case "find": self.$store.dispatch('createFind'); break;
				case "sample": self.$store.dispatch('createSample'); break;
			}
		},

		updateItem(item) {
			if(item) this.$store.dispatch('updateNode', item)

		},
			
		deleteItem(item) {
			let self = this
			self.$bvModal.msgBoxConfirm(`Delete ${ this.itemClass } ${item.data.id} - are you sure?`)
				.then(value => {
					if(value) self.$store.dispatch('deleteNode', item)
				})		
		},
		
		selectItem(item) {
			let self = this
			self.selectedID = item.data.id
		},
		
		rowSelected(items) {
			if(items.length > 0) {
				this.$emit('itemSelected', items[0])				
			}	
		}
		
	}
} 
</script>
<style scoped>
.action { 
	cursor: pointer;
	color:dodgerblue;
}
.action:hover {
	color:red;
}
</style>

