<template>
	<b-form-group name="toleranceGroup" label="Tolerance">      
        <b-input-group prepend="±" size="sm">
            <b-form-input size="sm" number
				:disabled="disabled"
                name="toleranceInput" 
                type="number" 
                min="0" 
                v-model="tolValue" /> 
            <b-input-group-append size="sm">
                <b-form-select size="sm" v-model="tolType" :options="tolTypes" :disabled="disabled"></b-form-select>
            </b-input-group-append>
        </b-input-group>
    </b-form-group>         
</template>

<script>

export default {
	name: 'YearTolerance',
	components: {
		//MyComponent 
	},
	mixins: [ ],	
	props: {
		disabled: {
			type: Boolean,
			required: false,
			default: false
		},
		toleranceValue: {
			type: Number,
			required: false,
			default: 0 
        },
        toleranceType: {
			type: String,
			required: false,
			default: "years" 
		}		
	},
	data() {
		return {
            tolTypes: [{ value: 'percent', text: '%' }, { value: 'years', text: 'years' }],
            tolValue: this.toleranceValue,            
            tolType: this.toleranceType.toString().trim().toLowerCase()
		}
	},
	computed: {},
	methods: {
		changed() {
			this.$emit('changed', { tolValue: this.tolValue, tolType: this.tolType });	
		}
	},
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

<style scoped>
p {
	color: blue;
	background: white;
}
</style>
