<template>
    <b-form>
	<!--<b-form-row>
        <b-col>
            <b-form-group 
                id="minValueGroup"
                label="Minimum year"
                label-for="minValueInput"
                valid-feedback="OK"
                :invalid-feedback="minValueInvalid"
                :state="minValueValidation">
                <b-form-input size="sm" id="minValueInput" type="number" number v-model="minValue" />                                 
            </b-form-group>
        </b-col> 
        <b-col>
            <YearTolerance toleranceValue="" toleranceType=""/>   
        </b-col>       
    </b-form-row>    
    <b-form-row>
        <b-col>
            <b-form-group 
                id="maxValueGroup"
                label="Maximum year"
                label-for="maxValueInput"
                valid-feedback="OK"
                :invalid-feedback="maxValueInvalid"
                :state="maxValueValidation">
                <b-form-input size="sm" id="maxValueInput" type="number" number v-model="maxValue" /> 
                                
            </b-form-group>
        </b-col>
        <b-col>
            <YearTolerance :toleranceValue="tolValue" :toleranceType="tolType"/>  
        </b-col>                  
	</b-form-row>-->
        <b-form-row>
            <b-col class="px-1">
                <b-form-group 
                    id="minValueGroup"
                    label="Minimum year"
                    label-for="minValueInput"
                    valid-feedback="OK"
                    :invalid-feedback="minValueInvalid"
                    :state="minValueValidation">
                    <b-form-input number
                        :disabled="this.disabled"
                        size="sm" 
                        name="minValueInput" 
                        type="number" 
                        v-model="minValue" 
                        @change="rangeChanged"/>                                 
                </b-form-group>
            </b-col>
            <b-col class="px-1">
                <b-form-group 
                    id="maxValueGroup"
                    label="Maximum year"
                    label-for="maxValueInput"
                    valid-feedback="OK"
                    :invalid-feedback="maxValueInvalid"
                    :state="maxValueValidation">
                    <b-form-input number 
                        :disabled="this.disabled"
                        size="sm" 
                        name="maxValueInput" 
                        type="number" 
                        v-model="maxValue" 
                        @change="rangeChanged"/>                               
                </b-form-group>
            </b-col>
            <b-col class="px-1">
                <YearTolerance 
                    :disabled="disabled"
                    :toleranceValue="tolValue" 
                    :toleranceType="tolType" 
                    @changed="tolChanged"/> 
            </b-col>
        </b-form-row>
    </b-form>
</template>

<script>
import PhaserCommon from '@/mixins/PhaserCommon.js'
import YearTolerance from '@/components/YearTolerance.vue'

export default {
	name: 'YearRangeEditor',
	components: {
        YearTolerance
	},
	mixins: [ PhaserCommon ],
	props: {
        range: {
            type: Object,
			required: false,
			default: null
        }		
    },
    watch: {
		/*range: {
            immediate: true,
            handler(newValue) {
				this.currentRange = newValue  
				//console.log(newValue)				
            }        
		}	*/
	},
	data() {
		return {
            minValue: ((this.range || {}).min || 0),   // min year
            maxValue: ((this.range || {}).max || 0),   // max year
            tolValue: ((this.range || {}).tol || 0),   // tolerance
            tolType: ((this.range || {}).tolType || "years") // "years" | "percent"
		}
	},
	computed: {
        disabled() { return this.range == null },        
        currentYear() {
            return new Date().getFullYear()
        },
        minValueValidation() {
            return Number.isInteger(this.minValue) 
                && this.minValue <= this.maxValue
                && this.minValue <= this.currentYear
        },
        maxValueValidation() {
            return Number.isInteger(this.maxValue) 
                && this.minValue <= this.maxValue
                && this.maxValue <= this.currentYear
        },
        minValueInvalid() {  
            if(!Number.isInteger(this.minValue))
                return "Please enter a valid year"
            else if(this.minValue > this.maxValue) 
                return "Min year cannot exceed max year"
            else if(this.minValue > this.currentYear)
                return "Min year cannot exceed current year"
            else
                return "something else wrong"
        },
        maxValueInvalid() {  
            if(!Number.isInteger(this.maxValue))
                return "Please enter a valid year"
            else if(this.minValue > this.maxValue) 
                return "Min year cannot exceed max year"
            else if(this.maxValue > this.currentYear)
                return "Max year cannot exceed current year"
            else
                return "something else wrong"
        }     
	},
	methods: {              
        tolChanged(data) {
            this.tolValue = data.tolValue
            this.tolType = data.tolType
            this.rangeChanged()
        },
        rangeChanged() {
            let newRange = {
                min: this.minValue, 
                max: this.maxValue, 
                tol: this.tolValue, 
                tolType: this.tolType
            } 
            this.$emit('change', newRange);	
        },
    },
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

<style scoped>
p {
	color: blue;
	background: white;
}
</style>
