<template>
	<ItemTable :items="items" :fields="fields" itemClass="find" @itemSelected="itemSelected"/>		
</template>

<script>
import ItemTable from '@/components/ItemTable.vue'

export default {
	name: 'FindsTable',
	components: {
		ItemTable 
	},
	mixins: [ ],
	props: { },
	data() {
		return {
            fields: [				
				{
					key: "data.id",
					label: "id",
					sortable: true					
				},
				{
					key: "data.label",
					label: "label",
					sortable: true					
				},
				{
					key: "data.parent",
					label: "parent",
					sortable: true					
                },
                {
					key: "data.type",
					label: "type",
					sortable: true					
				},
				{
					key: "data.dating.range.min",
					label: "min",
					sortable: true
				},
				{
					key: "data.dating.range.max",
					label: "max",
					sortable: true
				},
				{ 	
					key: "actions", 
					label: ""
				}
			]
		}
	},
	computed: {
        items() {
            return this.$store.getters.finds
		}
    },
	methods: {
		itemSelected(item) {
			this.$emit('itemSelected', item)
		}
	},
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

