<template>
	<b-tabs class="my-2" style="min-height:400px">
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-border-width />
				<span class="mx-2">Phases</span>
				<b-badge variant="secondary">{{ totalPhases }}</b-badge> 																	
			</template>
            <PhaseEditor/>
        </b-tab>
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-columns />
				<span class="mx-2">Groups</span>
				<b-badge variant="secondary">{{ totalGroups }}</b-badge> 																	
			</template>                
            <GroupEditor/>                
        </b-tab>  
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-columns />
				<span class="mx-2">Sub-Groups</span>
				<b-badge variant="secondary">{{ totalSubGroups }}</b-badge> 																	
			</template>                
            <SubGroupEditor/>                
        </b-tab>                  
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-bounding-box class="mr-2"/>
				<span class="mx-2">Contexts</span>
				<b-badge variant="secondary">{{ totalContexts }}</b-badge> 																	
			</template>
            <ContextEditor/>                
        </b-tab>
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-gem class="mr-2"/>
				<span class="mx-2">Finds</span>
				<b-badge variant="secondary">{{ totalFinds }}</b-badge> 																	
			</template>
            <FindsEditor/>              
        </b-tab>
        <b-tab class="my-2">
            <template v-slot:title>
				<b-icon-funnel class="mr-2"/>
				<span class="mx-2">Samples</span>
				<b-badge variant="secondary">{{ totalSamples }}</b-badge> 																	
			</template>
            <SamplesEditor/>                
        </b-tab>
    </b-tabs>
</template>

<script>
import PhaserCommon from '@/mixins/PhaserCommon.js'
import PhaseEditor from '@/components/PhaseEditor'
import GroupEditor from '@/components/GroupEditor'
import SubGroupEditor from '@/components/SubGroupEditor'
import ContextEditor from '@/components/ContextEditor'
import FindsEditor from '@/components/FindsEditor'
import SamplesEditor from '@/components/SamplesEditor'

export default {
	name: 'ItemTables',
	components: {
		PhaseEditor, 
        GroupEditor,
        SubGroupEditor,
        ContextEditor,
        FindsEditor,
        SamplesEditor
	},
	mixins: [ PhaserCommon ],
	props: {
		name: {
			type: String,
			required: false,
			default: "Fred" 
		}		
	},
	data() {
		return {
			
		}
	},
	computed: {	
		totalPhases(){ return this.$store.getters.phases.length },    
        totalGroups(){ return this.$store.getters.groups.length },  
        totalSubGroups(){ return this.$store.getters.subgroups.length },       
        totalContexts(){ return this.$store.getters.contexts.length },
        totalFinds(){ return this.$store.getters.finds.length },
        totalSamples(){ return this.$store.getters.samples.length },
	},
	methods: {},
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

<style scoped>
p {
	color: blue;
	background: white;
}
</style>