	},
	computed: {	},
	methods: {
		itemSelected(item) {
            this.selectedItem = item
        }
	},
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

