<template>
	<div class="m-2 text-left">
		<!--<div class="text-center" >{{ (( currentItem || {}).data || {}).id }}</div>-->
        <div class="text-center" >{{ ((currentItem || {}).data || {}).id }}</div>
		<b-form-group
			label="Label"
			label-for="itemLabel">	
			<!--<b-form-input id="itemParent" type="text" text v-model="itemParent" />-->
			<b-form-input text 
                :disabled="this.disabled"
				type="text"
				name="itemLabel" 
				v-model="((currentItem || {}).data || {}).label" 
				@change="labelChanged"/>
		</b-form-group>           		
		<b-form-group
			label="Type"
			label-for="itemType">	
			<b-form-input text
                :disabled="this.disabled"
				name="itemType" 
				type="text" 
				v-model="((currentItem || {}).data || {}).type" 
				list="phase-types" 
				autocomplete="off"/>
			<b-form-datalist id="phase-types">
				<option v-for="(value, index) in itemTypes" :key="index">{{ value }}</option>
			</b-form-datalist>
		</b-form-group>
		<!--<b-form-group
			label="Interpretation"
			label-for="itemDatingLabel">		
			<b-form-input text 
				id="itemDatingLabel" 
				type="text" 
				v-model="(((currentItem || {}).data || {}).dating || {}).label" />
		</b-form-group>-->
        <slot name="content"></slot>
		<b-form-group
			class="border px-2"
			label="Dating"
			label-for="itemDateRange">			
			<YearRangeEditor 
                name="itemDateRange"
				:range="(((currentItem || {}).data || {}).dating || {}).range"
				@change="dateRangeChanged"/>
			</b-form-group>        
	</div>
</template>

<script>
import PhaserCommon from '@/mixins/PhaserCommon.js'
import YearRangeEditor from '@/components/YearRangeEditor'

export default {
	name: 'PhaseFields',
	components: {
		YearRangeEditor 
	},
	mixins: [ PhaserCommon ],
	props: {		
		item: {
			type: Object,
			required: false,
			default: null
		}		
	},
	data() {
		return {			
			currentItem: this.item	
		}
	},
	computed: {
        disabled() { return this.currentItem == null },
		itemParents() { 
			return []
		},
		itemTypes() { 
			return this.$store.getters.phaseTypes
				.map(t => t)
				.sort((a,b) => a.localeCompare(b)) 
		}
	},
	watch: {
		item: {
            immediate: true,
            handler(newValue) {
				if(newValue) {
                    this.currentItem = newValue  
					//console.log(newValue)					
				}
            }        
		}	
	},
	methods: {	
		//getitem(itemID) {
			//this.currentItem = this.$store.getters.nodeById(itemID) 			
		//},
		labelChanged() {
			this.changed()
		},	
		dateRangeChanged(range) {	
			//console.log(range)
			if(this.currentItem) {		
				this.currentItem.data.dating.range = range
				this.changed()
			}
		},
		parentChanged() {
			//console.log("parentChanged: " + this.currentItem.data.parent)
			if(this.currentItem) {
				this.changed()
			}
		},
		changed(){
            this.$store.dispatch('updateNode', this.currentItem)
		},
	
	},
	// lifecycle hooks
	beforeCreate() {},
	created() {},
	beforeMount() {},
	mounted() {},
	beforeUpdate() {},
	updated() {},
	beforeDestroy() {},
	destroyed() {}
}
</script>

